@import "variables";
@import "mixin";

.container {
  margin: 50px auto;
}

.catalog-cards {
  display: grid;
  grid-gap: 51px 48px;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
}

.card {
  width: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 14px;
  background: #fff;
  border: 1px solid #f3f3f3;
  box-sizing: border-box;
  border-radius: 5px;

  &__img {
    width: 160px;
    height: 134px;
    margin: 0 auto 20px;
    padding: 13px 0 22px;
    border: 3px solid transparent;
    transition: transform 1s;
  }

  &__img:hover {
    border-color: #f3f3f3;
    transform: scale(1.2);
  }

  &__text {
    @include primeryFont;
  }

  &__code-product {
    margin-top: 5px;
    margin-bottom: 15px;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin-bottom: 20px;
  }

  &__price {
    @extend .card__reviews;
    align-items: baseline;

    .cost {
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
    }
  }

  &__button {
    width: 100%;
    height: 40px;
    background-color: $important-color;
    border-radius: 5px;
    box-sizing: border-box;
    font-weight: 500;
    font-size: 14px;
    line-height: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
    border: 1px solid transparent;
    transition: background 0.3s, color 0.3s, border-color 0.3s;
  }

  &__button:hover {
    border-color: $important-color;
    background: #fff;
    color: $color-link-active;
  }
}
